import React, { useState, useContext, useRef, useEffect } from 'react';

import { InputFieldsContext } from '../../store/InputFieldsStore';
import logoAbout from '../../assets/images/logo-about.svg';
import './Form.css';
import { useSpring, animated } from 'react-spring';

const Form = ({ showForm, complete }) => {

    const [inputValues, onNameChange, onEmailChange] = useContext(InputFieldsContext);
    const [question, setQuestion] = useState('');
    const [submitButtonClicked, setSubmitButtonClicked] = useState(false);
    const [buttonAreaHeight, setButtonAreaHeight] = useState(0);
    const ref = useRef();

    useEffect(() => {
        const ro = new ResizeObserver(([entry]) => {
            setButtonAreaHeight(entry.target.scrollHeight);
        });

        if (ref.current) {
            ro.observe(ref.current)
        }
    
        return () => {
            ro.disconnect();
        }
    }, [ref]);

    const checkQuestion = (question) => {
        if (question.length > 3) {
            return true;
        } else {
            return false;
        }
    }

    const checkName = (name) => {
        if (name && (name.length > 1)) {
            return true;
        } else {
            return false;
        }
    }

    const checkEmail = (email) => {
        let re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (re.test(email)) {
            return true;
        } else {
            return false;
        }
    }

    const isReadyToBeSubmitted = (checkName(inputValues.name) && checkEmail(inputValues.email) && checkQuestion(question));

    let buttonStyle = 'enabled submit absolute form-inputs-width grad-bg pointer white container border-radius';
    if (!isReadyToBeSubmitted) {
        buttonStyle = 'disabled-btn submit absolute form-inputs-width container border-radius';
    }

    let formClasses = 'hide'
    if (showForm) {
        formClasses = 'form no-blur border-radius white-bg flex-column-centered-row show'
    }

    const onQuestionChange = (event) => {
        let question = event.target.value;
        setQuestion(question);
    }

    const getButtonStyle = () => {
        if (submitButtonClicked) {
            return ' checked';
        } else {
            return '';
        }
    }

    const submitButtonClickHandler = () => {
        if (isReadyToBeSubmitted) {
            setSubmitButtonClicked(!submitButtonClicked);
            setTimeout(() => {
                complete();
                setSubmitButtonClicked(false);
            }, 4500);
            fetch('http://localhost:5000/ask', {
                method: 'post',
                headers: {'Content-Type': 'application/json'},
                body: JSON.stringify({
                    name: inputValues.name,
                    email: inputValues.email,
                    question: question,
                })
            }).catch(err => {
                console.log('Unable to ask the question')
            });
        }
    }

    const props = useSpring({
        from: { opacity: 0, height: 0 },
        to: { opacity: isReadyToBeSubmitted ? 1 : 0, height: isReadyToBeSubmitted ? buttonAreaHeight : 0 },
    });

    return (
        <div className={formClasses}>

            <img src={logoAbout} alt="Лого" className="form-logo" />

            <div className="input-fields">
                <div style={{marginTop: 10}} className="flex-column relative input-margin-bottom">
                    <input type="text" className="form-inputs-width" onChange={onNameChange} value={inputValues.name} required/>
                    <label>Ваши имя и фамилия</label>
                </div>

                <div className="flex-column relative input-margin-bottom">
                    <input type="text" className="form-inputs-width" onChange={onEmailChange} value={inputValues.email} required/>
                    <label>Ваша электронная почта</label>
                </div>

                <div className="flex-column relative input-margin-bottom">
                    <textarea type="" className="form-inputs-width" onChange={onQuestionChange} required/>
                    <label>Ваш вопрос</label>
                </div>
            </div>

            <animated.div style={props}>
                <div ref={ref}>
                <div className={"button-wrapper container white full-width" + getButtonStyle()}>
                    <div className={buttonStyle} onClick={submitButtonClickHandler}>
                        Спросить
                    </div>
                    <div className="loader absolute loader-violet" />
                    <div className="loader absolute loader-blue" />
                    <div className="check-wrapper absolute">
                        <div className="checkmark" />
                    </div>
                </div>
                <p className="form-inputs-width line-height text-centered" style={{marginBottom: '1em'}}>Нажимая на кнопку, вы даете согласие на обработку персональных данных и соглашаетесь c политикой конфиденциальности</p>
                </div>
            </animated.div>
        </div>
    );
}

export default Form;